$("#popiconimg1").click(function(){
    var p="../logistics/serviceRental/service/fur.jpg";
    let pop = opg.popTop(`<image style="width:100%;" src="`+p+`"/>` , {
        title: `${lpg.furniturepic}`,
        btnMax: false,
        width: 500,
        height: 650,
        buttons: {
            download: {
                className: 'btn-success',
                text: lpg.download,
                onClick: function (i, ifr) {
                    window.open(p);
                    return true;
                }
            },
            cancel: lpg.cancel
        },
        onClose: function () {

        }
    });
});

