function sendResource(resource, pop: PopUp,pop2:PopUp) {
	opg.api.addResource(resource, function (data) {
		pop.close();
		pop2.close();
		refreshtable();
        setTimeout(function () {
            refreshtable2();
        }, 1000);

        setTimeout(function () {
            refreshtable3();
        }, 1500);
	});
}